import React from 'react';
import {
	Checkbox,
	TableCell,
	TableHead,
	TableSortLabel,
	TableRow,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { visuallyHidden } from '@material-ui/utils';

import { EnhancedTableHeadWithMassActionProps } from '../../../types';
import { EnhancedTableToolbar } from './ListToolbar';

const headCells = [
	{
		id: 'FormId',
		numeric: false,
		label: 'ID',
		align: 'left',
		disablePadding: true,
	},
	{
		id: 'Title',
		numeric: false,
		label: 'Title',
		align: 'left',
		disablePadding: false,
	},
	{
		id: 'Category',
		numeric: false,
		label: 'Category',
		align: 'left',
		disablePadding: false,
	},
	{
		id: 'FormType',
		numeric: false,
		label: 'Form Type',
		align: 'left',
	},
	{
		id: 'CreatedDT',
		numeric: false,
		label: 'Created Date',
		align: 'left',
	},
	{
		id: 'UpdatedDT',
		numeric: false,
		label: 'Updated Date',
		align: 'left',
	},
];

const useStyles = makeStyles(() => ({
	sortSpan: { ...visuallyHidden },
}));

const ListHeader = (props: EnhancedTableHeadWithMassActionProps) => {
	const classes = useStyles();
	const {
		onSelectAllClick,
		order,
		orderBy,
		numSelected,
		rowCount,
		onRequestSort,
		onDelete,
	} = props;
	const createSortHandler = (form: string) => (event: React.SyntheticEvent) => {
		onRequestSort(event, form);
	};

	return (
		<TableHead>
			<TableRow>
				<TableCell padding='checkbox' sx={{ pl: 3 }}>
					<Checkbox
						color='primary'
						indeterminate={numSelected > 0 && numSelected < rowCount}
						checked={rowCount > 0 && numSelected === rowCount}
						onChange={onSelectAllClick}
						inputProps={{
							'aria-label': 'select all desserts',
						}}
					/>
				</TableCell>
				{numSelected > 0 && (
					<TableCell padding='none' colSpan={12}>
						<EnhancedTableToolbar numSelected={numSelected} onDelete={onDelete} />
					</TableCell>
				)}
				{numSelected <= 0 &&
					headCells.map(headCell => (
						<TableCell
							key={headCell.id}
							align={headCell.numeric ? 'right' : 'left'}
							padding={headCell.disablePadding ? 'none' : 'normal'}
							sortDirection={orderBy === headCell.id ? order : undefined}
						>
							<TableSortLabel
								active={orderBy === headCell.id}
								direction={orderBy === headCell.id ? order : 'asc'}
								onClick={createSortHandler(headCell.id)}
							>
								{headCell.label}
								{orderBy === headCell.id ? (
									<span className={classes.sortSpan}>
										{order === 'desc' ? 'sorted descending' : 'sorted ascending'}
									</span>
								) : null}
							</TableSortLabel>
						</TableCell>
					))}
				{numSelected <= 0 && (
					<TableCell sortDirection={false} align='center' sx={{ pr: 3 }}>
						Action
					</TableCell>
				)}
			</TableRow>
		</TableHead>
	);
};

export default ListHeader;
